'use client';

import { useEffect, useState } from 'react';
import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Checkbox } from '@/src/components/ui/checkbox';
import { Button } from '@/src/components/ui/button';
import { SimpleSelect } from '@/src/components/ui/select';
import { getFormConfigurationByCompanyId } from '@/src/lib/services/Configirations/formConfigurationService';
import { FormField, FormConfiguration } from '@/src/lib/formConfigurations/formConfigurationTypes';
import { Timestamp, addDoc, collection, getDocs, query, where, doc, getDoc } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import IDUploader from '@/src/components/rent/common/IDUploader';
import { Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikemodels/ebikeModelTypes';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import BatterySelector from './common/BatterySelector';
import PackageSelector from './common/PackageSelector';

interface Props {
  companyId: string;
  userId: string;
}

export default function DynamicRentalForm({ companyId, userId }: Props) {
  const [config, setConfig] = useState<FormConfiguration | null>(null);
  const [formData, setFormData] = useState<Record<string, any>>({});
  const [loading, setLoading] = useState(true);
  const [allBikes, setAllBikes] = useState<(Ebike & { modelName?: string })[]>([]);
  const [bikeSuggestions, setBikeSuggestions] = useState<(Ebike & { modelName?: string })[]>([]);
  const [packages, setPackages] = useState<SubscriptionPackage[]>([]);

  useEffect(() => {
    const fetchData = async () => {
      const configSnap = await getFormConfigurationByCompanyId(companyId);
      setConfig(configSnap);

      const bikeSnap = await getDocs(query(collection(db, 'ebikes'), where('companyId', '==', companyId)));
      const bikes = await Promise.all(
        bikeSnap.docs.map(async (docSnap) => {
          const { id: _removedId, ...bikeData } = docSnap.data() as Ebike; // X√≥a id c≈©
          let modelName = '';
          try {
            const modelSnap = await getDoc(doc(db, 'ebikeModels', bikeData.modelId));
            modelName = modelSnap.exists() ? (modelSnap.data() as EbikeModel).name : '';
          } catch {
            modelName = '';
          }
          return { id: docSnap.id, ...bikeData, modelName };  // Gi·ªù id l√† docSnap.id ch√≠nh x√°c
        })
      );
      
      setAllBikes(bikes);

      const packageSnap = await getDocs(query(collection(db, 'subscriptionPackages'), where('companyId', '==', companyId)));
      const packages = packageSnap.docs.map(doc => ({ id: doc.id, ...doc.data() })) as SubscriptionPackage[];
      setPackages(packages);

      setLoading(false);
    };

    fetchData();
  }, [companyId]);

  const handleChange = (key: string, value: any) => {
    setFormData(prev => {
      const updated = { ...prev, [key]: value };
      if (key.startsWith('package')) {
        const selectedPackage = packages.find(pkg => pkg.id === value);
        if (selectedPackage) {
          updated['basePrice'] = selectedPackage.basePrice;
          updated['kmLimit'] = selectedPackage.kmLimit ?? 'Unlimited';
          updated['overageRate'] = selectedPackage.overageRate ?? '-';
          updated['chargingMethod'] = selectedPackage.chargingMethod;
        }
      }
      return updated;
    });
  };

  const handleSubmit = async () => {
    try {
      await addDoc(collection(db, 'rentalOrders'), {
        ...formData,
        companyId,
        createdBy: userId,
        createdAt: Timestamp.now(),
      });
      alert('‚úÖ Booking submitted successfully!');
    } catch (err) {
      console.error('‚ùå Submit error:', err);
      alert('Failed to submit booking.');
    }
  };

  const populateVehicleSuggestions = (search: string) => {
    const suggestions = allBikes.filter(bike =>
      bike.vehicleID?.toLowerCase().includes(search.toLowerCase()) ||
      bike.plateNumber?.toLowerCase().includes(search.toLowerCase()) ||
      bike.modelName?.toLowerCase().includes(search.toLowerCase())
    );
    setBikeSuggestions(suggestions);
  };

  const handleSelectBike = (bike: Ebike & { modelName?: string }) => {
    handleChange('vehicleSearch', bike.vehicleID);
    handleChange('vehicleModel', bike.modelName || '');
    handleChange('vehicleColor', bike.color || '');
    handleChange('vin', bike.vehicleID || '');
    handleChange('licensePlate', bike.plateNumber || '');
    setBikeSuggestions([]);
  };

  if (loading || !config) return <div>Loading form...</div>;

  const renderField = (field: FormField) => {
    if (!field.visible) return null;

    if (field.key === 'idImage') {
      return (
        <IDUploader
          key={field.key}
          onExtracted={({ name, idNumber }) => {
            handleChange('fullName', name);
            handleChange('idNumber', idNumber);
          }}
        />
      );
    }

    if (field.key === 'vehicleSearch') {
      return (
        <div key={field.key} className="space-y-2 relative">
          <Input
            placeholder="Search by VIN / Plate / Model"
            value={formData[field.key] || ''}
            onChange={(e) => {
              const value = e.target.value;
              handleChange(field.key, value);
              populateVehicleSuggestions(value);
            }}
          />
          {bikeSuggestions.length > 0 && (
            <ul className="absolute z-10 w-full bg-white border rounded shadow-md mt-1 max-h-48 overflow-y-auto">
              {bikeSuggestions.map((bike) => (
                <li
                  key={bike.id}
                  className="cursor-pointer px-4 py-2 hover:bg-gray-100"
                  onClick={() => handleSelectBike(bike)}
                >
                  <div className="font-semibold">{bike.vehicleID} {bike.plateNumber && `- ${bike.plateNumber}`}</div>
                  <div className="text-sm text-gray-500">{bike.modelName || 'Unknown Model'}</div>
                </li>
              ))}
            </ul>
          )}
        </div>
      );
    }

    if (field.key === 'vehicleModel') {
      return (
        <Input
          key={field.key}
          placeholder="Vehicle Model"
          value={formData[field.key] || ''}
          readOnly
          className="bg-gray-100 text-gray-600"
        />
      );
    }

    if (field.key.startsWith('batteryCode')) {
      return (
        <BatterySelector
          key={field.key}
          value={formData[field.key] || ''}
          onChange={(val) => handleChange(field.key, val)}
        />
      );
    }

    if (field.key.startsWith('package')) {
      return (
        <PackageSelector
          key={field.key}
          value={formData[field.key] || ''}
          onChange={(val) => handleChange(field.key, val)}
          companyId={companyId}
        />
      );
    }

    switch (field.type) {
      case 'text':
      case 'number':
      case 'date':
        return (
          <Input
            key={field.key}
            type={field.type}
            placeholder={field.label}
            value={formData[field.key] || ''}
            onChange={(e) => handleChange(field.key, e.target.value)}
          />
        );
      case 'textarea':
        return (
          <Textarea
            key={field.key}
            placeholder={field.label}
            value={formData[field.key] || ''}
            onChange={(e) => handleChange(field.key, e.target.value)}
          />
        );
      case 'checkbox':
        return (
          <label key={field.key} className="flex items-center gap-4">
            <Checkbox
              checked={!!formData[field.key]}
              onCheckedChange={(val) => handleChange(field.key, Boolean(val))}
            />
            <span>{field.label}</span>
          </label>
        );
      case 'select':
        return (
          <SimpleSelect
            key={field.key}
            options={(field.options || []).map(opt => ({ label: opt, value: opt }))}
            value={formData[field.key] || ''}
            onChange={(val) => handleChange(field.key, val)}
            placeholder={field.label}
          />
        );
      case 'upload':
        return (
          <Input
            key={field.key}
            type="file"
            onChange={(e) => handleChange(field.key, e.target.files?.[0])}
          />
        );
      default:
        return null;
    }
  };

  return (
    <div className="space-y-6">
      {config.sections.map((section) => {
        const visibleFields = section.fields.filter((f) => f.visible);
        if (!visibleFields.length) return null;

        return (
          <div key={section.id} className="border rounded-lg p-4 bg-white">
            <h3 className="font-semibold text-lg mb-3">{section.title}</h3>
            <div
              className={
                section.id === 'accessories_info'
                  ? 'grid grid-cols-1 gap-4'
                  : 'grid grid-cols-1 md:grid-cols-2 gap-3'
              }
            >
              {visibleFields.map(renderField)}
            </div>
          </div>
        );
      })}

      <div className="flex flex-col sm:flex-row gap-4 mt-6 p-4">
        <Button onClick={handleSubmit} className="flex-1">
          ‚úÖ Confirm Booking
        </Button>
        <Button variant="outline" onClick={() => window.print()} className="flex-1">
          üñ®Ô∏è Print Invoice
        </Button>
      </div>
    </div>
  );
}
